export default {
    name: 'NewsSwiper',
    data(){
        return{
            swiperOption: {
                slidesPerView: 3,
                spaceBetween: 30,
                breakpoints: {
                    1024: {
                        slidesPerView: 2,
                        spaceBetween: 15,
                    },
                    500: {
                        slidesPerView: 1,
                        spaceBetween: 0,
                    }
                },
                navigation: {
                    nextEl: '.news-swiper-outside .swiper-button-next',
                    prevEl: '.news-swiper-outside .swiper-button-prev'
                }
            },
            testData: []
        }
    },
    methods: {
        scrollToPageTop() {
            window.scrollTo({ 
                top: 0, 
                behavior: "smooth" 
            });
        },
        goToNewsContent(id) {
            this.$router.push(`/maxBeauty/news/${id}`);
            this.scrollToPageTop()
        },
        openNewTab(id) {
            const routerData = this.$router.resolve({path: `/maxBeauty/news/${id}`});
            window.open(routerData.href, '_blank');
        },
    },
    mounted() {
        let arr = [1,2,3,4,5,6];
        arr.forEach((item)=>{
            this.testData.push({
                id: item,
                imgsrc: require('~/assets/img/news.jpg'),
                time: `2021-03-1${item}`,
                title: '新冠肺炎來勢洶洶  美仕媞與您一同守護您的安全及健康',
                detail: '新冠肺炎疫情爆發，美仕媞時尚醫美診所農曆年後實施各項措施，包含全面加強消毒防疫、每日定時以醫療用次氯酸水淨化空氣及酒精消毒環境加強防疫，員工每日健康自主檢查、加強衛教及配戴口罩，每位貴賓入店時確實測量體溫'
            })
        })
    }
}
